*{
    margin: 0;
    padding: 0;
}
.top{
    position: fixed;
    top:0;
    width: 100%;
    height: 36px;
    z-index: 2;
}

.top .am-search{
    color: white;
    height: 40px;
}

.top .am-search-input{
    background-color: rgb(255, 78, 34);
    font-size: 1px;

}

.top .am-search-input .am-search-synthetic-ph-placeholder{
    color: white;
    font-size: 13px;  
}

.roul{
    margin-top: 40px;
    z-index: 1;
}

.contents{
    width:100%;
    background: #f2f2f2;
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
    position: relative;
    margin-bottom: 60px;
    overflow: hidden;
}
.contents .con{
    width:171px;
    height:260px;
    float: left;
    background: #fff;
    position: relative;
    margin: 9px 4.5px 3px 8.5px;
    border-radius: 6px;
    /* border: 1px solid red; */

}
.contents .con img{
    width:170px;
    height:170px;
    border-radius: 8px;
    /* border: 1px solid red; */
}
.details{
    width: 100%;
    /* height: 400px; */
    
}
.last{
    width: 100%;
    height: 60px;
    /* border: 1px solid red; */
    position: fixed;
    bottom: 0;
}
.tabs .tabbox{
    height: 47px;
    background-color: white;
    
   
}
.tabs .tabbox .left{
    float: left;
    width: 47%;
    border-radius: 20px 0 0 20px;
    background-image: linear-gradient(to right, #FFC500, #FF9402);
    height: 40px;
    margin-top: 8px;
    margin-left: 6px;
    color: white;
    text-align: center;
    line-height: 40px;
     /* border: 1px solid red; */
}
.tabs .tabbox .right{
    float: left; 
    width: 47%;
    border-radius: 0 20px 20px 0;
    background-image: linear-gradient(to right, #FF7A00, #FE560A);
    height: 40px;
    margin-top: 8px;
    color: white;
    text-align: center;
    line-height: 40px;
}

